import React, {
  useEffect, useState, useRef, useCallback,
} from 'react';
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import { GET_PRODUCTS } from '../../../constants/actionTypes';

import Product from './Product';
import agent from '../../../agent';

const RenderInfiniteScrollProduct = (props) => {
  const [loading, setLoading] = useState(false);

  const [products, setProducts] = useState([]);
  const [hasMore, setHasMore] = useState(true);
  const [pageNumber, setPageNumber] = useState(1);

  const observer = useRef();
  const lastProductElementRef = useCallback((node) => {
    if (loading) return;
    if (observer.current) {
      observer.current.disconnect();
    }
    observer.current = new IntersectionObserver((entries) => {
      if (entries[0].isIntersecting && hasMore) {
        setPageNumber((prevPageNumber) => prevPageNumber + 1);
      }
    });
    if (node) observer.current.observe(node);
  }, [loading, hasMore]);

  useEffect(() => {
    
    if (props.username) {
      setLoading(true);
      const data = { username: props.username };
      props.getProducts(data, pageNumber);
    }
  }, [pageNumber, props.username]);
  useEffect(() => {
    if (props.products) {
      
      setHasMore(props.products.length !== products.length);
      setProducts(props.products);
      setLoading(false);
    }
  }, [props.products]);
  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      <div className="flex flex-wrap justify-center">
        {
              products.map((p, i) => {
                if (i + 1 === products.length) {
                  return (
                    <div ref={lastProductElementRef} className='admin-product' >
                      <Product data={p} key={i} style={{width:'100%',margin:0}} />
                    </div>
                  );
                }
                return (
                  <Product data={p} key={i} />
                );
              })
        }
      </div>
      {
              loading
                ? (
                  <CircularProgress style={{
                    width: '40px', height: '40px',
                  }}
                  />
                )
                : null
          }
    </div>
  );
};


const mapStateToProps = (state) => ({
  products: state.ProductsPageReducers.products,
});

const mapDispatchToProps = (dispatch) => ({
  getProducts: (user, pageNumber) => dispatch({ type: GET_PRODUCTS, pageNumber, payload: agent.ProductsPage.getProducts(user, pageNumber) }),
});


export default connect(mapStateToProps, mapDispatchToProps)(RenderInfiniteScrollProduct);
