import React, {
  useEffect, useState, useRef, useCallback,
} from 'react';
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import { GET_PUBLIC_PAGE_PRODUCTS } from '../../../constants/actionTypes';

import Product from './Product';
import agent from '../../../agent';

const RenderInfiniteScrollProduct = (props) => {
  const [loading, setLoading] = useState(false);
  const [products, setProducts] = useState([]);
  const [hasMore, setHasMore] = useState(true);
  const [pageNumber, setPageNumber] = useState(1);

  const observer = useRef();
  const lastProductElementRef = useCallback((node) => {
    if (loading) return;
    if (observer.current) {
      observer.current.disconnect();
    }
    observer.current = new IntersectionObserver((entries) => {
      if (entries[0].isIntersecting && hasMore) {
        console.log("prev page number",pageNumber)
        setPageNumber((prevPageNumber) => prevPageNumber + 1);
      }
    });
    if (node) observer.current.observe(node);
  }, [loading, hasMore]);

  useEffect(() => {
    if (props.parent === 'public-home-page' && props.username) {
      setLoading(true);
      const data = { username: props.username };
      props.getPublicPageProducts(data, pageNumber);
    }
  }, [pageNumber, props.username]);
  useEffect(() => {
    
    if (props.parent === 'admin-page') {
      if (props.products) {
        
        setHasMore(props.products.length !== products.length);
        setProducts(props.products);
        setLoading(false);
      }
    } else if (props.publicPageProducts) {
      
      setHasMore(props.publicPageProducts.length !== products.length);
      setProducts(props.publicPageProducts);
      setLoading(false);
    }
  }, [props.publicPageProducts, props.products]);
  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      {
            products.length > 0
              ? (
                <div className="home-page-products-div w-100" style={{ justifyContent: 'space-evenly' }}>
                  <div className="home-page-products-top-div">
                    <h3>Our Products</h3>
                  </div>
                  <div className="home-page-products-content" style={{ justifyContent: 'space-evenly' }}>
                    {
                        products.map((p, i) => {
                          if (i + 1 === products.length) {
                            return (
                              <div className="public-product relative" ref={lastProductElementRef}>
                                <Product data={p} key={p} parent={!props.parent ? 'public-home-page' : props.parent} style={{width:'100%',margin:0}} />
                              </div>
                            );
                          }
                          return <Product data={p} key={p.id} parent={!props.parent ? 'public-home-page' : props.parent} />;
                        })
                    }
                  </div>
                </div>
              ) : null
        }

      {
            loading
              ? (
                <CircularProgress style={{
                  width: '40px', height: '40px',
                }}
                />
              )
              : null
        }
    </div>
  );
};


const mapStateToProps = (state) => ({
  products: state.ProductsPageReducers.products,
  publicPageProducts: state.HomePageReducers.publicPageProducts,
});

const mapDispatchToProps = (dispatch) => ({
  getPublicPageProducts: (user, pageNumber) => dispatch({ type: GET_PUBLIC_PAGE_PRODUCTS, pageNumber, payload: agent.ProductsPage.getProducts(user, pageNumber) }),
});


export default connect(mapStateToProps, mapDispatchToProps)(RenderInfiniteScrollProduct);
