/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { toast } from 'react-toastify';
import CloseIcon from '@material-ui/icons/Close';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

import './HomePage.css';
import agent from '../../agent';
import ProfilePIc from '../../common/Components/ProfilePIc';
import Loader from '../../common/Components/Loader';
import { store } from '../../store';
import {
  GET_USER_DETAILS, GET_PUBLIC_PAGE_HEADER_INFO, GET_PUBLIC_PAGE_USER, CLEAR_CART, GET_PUBLIC_PAGE_PRODUCTS, GET_PUBLIC_PAGE_SHOP_DETAILS,
} from '../../constants/actionTypes';
import Logo from '../../common/Components/Logo';
import { DEFAULT_BG_STYLE, DEFAULT_TEXT_COLOR } from '../../constants/otherConstants';
import ChangeProfilePic from '../../common/Components/ChangeProfilePic';
import CollapseTransition from '../../common/Components/CollapseTransition';
import lightOrDark from '../../common/Components/checkBgColor';
// import Product from './components/Product';
import CartDetails from './components/CartDetails';
import Image from '../../common/Components/Image';
import RenderInfiniteScrollProduct from './components/RenderInfiniteScrollProduct';

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      img: null,
      loading: false,
      user: { profile_pic: null, link: [], username: '' },
      error: '',
      bgStyle: null,
      togelSignUpMessage: true,
      textColor: DEFAULT_TEXT_COLOR,
      products: [],
      render: false,
      preview_image: '',
      shippingAddress:"",
    };
    this.links = [];
    this.cartItems = [];
  }

  componentWillMount() {
    // toast.info("World Wide Shipping !!!")
    const { 
      parent, links, user, shippingAddress, homeBgStyle, products, cartItems, clearCart, setPublicPageUserDetails, 
    } = this.props;
    if (parent) {
      if (parent === 'admin-page') {
        if (links) {
          this.links = links.links;
        }
        if (user) {
          this.setState({ user: user.user, togelSignUpMessage: false });
          setPublicPageUserDetails({ user: user.user });
        }

        if (homeBgStyle) {
          let data = JSON.parse(DEFAULT_BG_STYLE);
          let textColor = DEFAULT_TEXT_COLOR;
          if (homeBgStyle.background_color) {
            try {
              data = JSON.parse(homeBgStyle.background_color);
              textColor = lightOrDark(data.backgroundColor);
            } catch {
              data = JSON.parse(DEFAULT_BG_STYLE);
            }
          }
          if (homeBgStyle.link_style) {
            textColor = homeBgStyle.link_style;
          }
          if (homeBgStyle.background_image) {
            data = { ...data, backgroundImage: `${homeBgStyle.background_image}` };
          }
          this.setState({ bgStyle: data, togelSignUpMessage: false, textColor });
        }
        if (products && products.products) {
          this.setState({ products: products.products });
        }
      }
    } else {
      // other api callls
      const {
        getPublicPageHeaderInfo, getPublicPageUserDetails, match, getPublicPageShopDetails,
      } = this.props;
      const { userName } = match.params;
      const data = { username: userName };
      agent.AdminPage.addViewToProfile(data);
      this.setState({ loading: true });
      getPublicPageHeaderInfo(data);
      getPublicPageUserDetails(data);
      getPublicPageShopDetails(data);
      agent.AdminPage.getBackground(data).then((res) => {
        let textColor = DEFAULT_TEXT_COLOR;
        let data = {};
        let preview_image = '';
        if (res.data) {
          if (res.data.background_color) {
            try {
              data = JSON.parse(res.data.background_color);
              textColor = lightOrDark(data.backgroundColor);
            } catch {
              data = JSON.parse(DEFAULT_BG_STYLE);
            }
          } else {
            data = JSON.parse(DEFAULT_BG_STYLE);
          }
          if (res.data.link_style) {
            textColor = res.data.link_style;
          }
          if (res.data.background_image) {
            data = { ...data, backgroundImage: `${res.data.background_image}` };
            preview_image = res.data.preview_image;
          }
          this.setState({ bgStyle: data, textColor, preview_image });
        } else {
          this.setState({ bgStyle: JSON.parse(DEFAULT_BG_STYLE), textColor });
        }
      });
      this.setState({ loading: true });
    }

    // cart APi
    // cart Items related info
    if (cartItems) {
      this.cartItems = cartItems;
      if (this.state.user) {
        if (this.cartItems.filter((i) => i.item.user !== this.state.user.id).length > 0) {
          clearCart();
        }
      }
      this.setState({ render: !this.state.render });
    }
  }

  componentWillReceiveProps(np) {
    const { shippingAddress, user, render } = this.state;
    const { clearCart, parent, setPublicPageUserDetails } = this.props;
    if (parent === 'admin-page') {
      if (np.links && np.links.links !== this.links) {
        this.links = np.links.links;
        this.setState({ loading: false });
      }
      if (np.user && np.user.user !== this.state.user) {
        this.setState({ user: np.user.user });
        setPublicPageUserDetails({ user: np.user.user });
      }
      if (np.homeBgStyle) {
        let data = {};
        let textColor = DEFAULT_TEXT_COLOR;
        if (np.homeBgStyle.background_color) {
          try {
            data = JSON.parse(np.homeBgStyle.background_color);
            textColor = lightOrDark(data.backgroundColor);
          } catch {}
        }

        if (np.homeBgStyle.link_style) {
          textColor = np.homeBgStyle.link_style;
        }

        if (np.homeBgStyle.background_image) {
          data = { ...data, backgroundImage: `${np.homeBgStyle.background_image}` };
        }
        this.setState({ bgStyle: data, textColor });
      }
      if (np.products && np.products.products) {
        this.setState({ products: np.products.products });
      }
      this.setState({ loading: false });
    } else {
      
      if (np.publicPageUser) {
        this.links = np.publicPageUser.links;
        this.setState({ user: np.publicPageUser, loading: false });
      }
      if (np.publicPageProducts) {
        this.setState({ products: np.publicPageProducts });
      }
      if (np.publicPageShopDetails && np.publicPageShopDetails.address !== shippingAddress) {
        this.setState({ shippingAddress: np.publicPageShopDetails.address });
        toast.info(np.publicPageShopDetails.address);
      }
    }
    if (np.cartItems && np.cartItems !== this.cartItems) {
      this.cartItems = np.cartItems;
      if (user) {
        if (this.cartItems.filter((i) => i.item.user !== user.id).length > 0) {
          clearCart();
        }
      }
      this.setState({ render: !render });
    }
  }

  addClickToLink(link) {
    if (this.props.parent !== 'admin-page') {
      agent.AdminPage.addClickToLink({ link_id: link.id });
    }
  }

  render() {
    const { username, profile_pic } = this.state.user;
    const { textColor, preview_image } = this.state;
    const bgStyle = { position: 'fixed', width: window.innerWidth > window.innerHeight ? window.innerWidth : '100%', height: window.innerHeight > window.innerWidth ? window.innerHeight : 'auto' };

    return (
      <div className="gradientbg home-page-bg mh-100 " style={this.state.bgStyle ? { ...this.state.bgStyle } : null}>
        <div style={bgStyle}>
          {
            this.state.bgStyle ? <Image src={this.state.bgStyle.backgroundImage} style={bgStyle} overlaySrc={preview_image} /> : null
          }
        </div>

        {
                this.state.products.length > 0
                  ? (
                    <div className="home-page-nav-div">
                      <Logo
                        logostyles={{
                          display: 'flex', flexDirection: 'row', color: 'white',
                        }}
                        parent={!this.props.parent ? 'public-home-page' : this.props.parent}
                        username={username}
                      />
                      <button className="home-page-cart-div relative" type="button">
                        <ShoppingCartIcon style={{ color: 'white' }} />
                        <CartDetails />
                        <span style={{ color: 'white', fontWeight: 'bold' }}>
                          Cart(
                          {this.cartItems.length}
                          )
                        </span>
                      </button>

                    </div>
                  )
                  : null
              }
        <div className="  ">
          <div className="dyna-width">
            <div className=" center">


              <article className="center  tc ">
                {
                  this.props.parent === 'admin-page' ? <ChangeProfilePic /> : <ProfilePIc photo={profile_pic} changeProfile={false} />
                }
                <h1 className="subheading white center tc relative" style={{ color: textColor }}>
                  {`@${username}`}
                </h1>
                <p className="err-txt">{`${this.state.error}`}</p>
              </article>
            </div>
            <div className="tc mt4">
              {
                this.links.map((link, i) => {
                  if (link.visible) {
                    return (
                      <a href={link.url} onClick={() => this.addClickToLink(link)} key={i} className="link " target="_blank" rel="noopener noreferrer" style={{ color: textColor, border: `2px solid ${textColor}` }}>
                        {link.icon ? <img src={link.icon} alt="" /> : null}
                        <p className="center ">
                          {`${link.title}`}
                        </p>
                      </a>
                    );
                  }
                  return null;
                })
              }

            </div>
          </div>


          <RenderInfiniteScrollProduct parent={!this.props.parent ? 'public-home-page' : this.props.parent} username={username} />


        </div>

        <footer className="">

          <Logo
            logostyles={{
              display: 'flex', flexDirection: 'row', margin: 'auto', color: this.state.textColor,
            }}
            parent={!this.props.parent ? 'public-home-page' : this.props.parent}
            username={username}
          />

          <CollapseTransition visible={this.state.togelSignUpMessage}>
            <div className="signup-message relative " style={{ zIndex: 50 }}>
              <p>
                Want a similar page of your own?
                <a href="/login">Click here to signup</a>
              </p>
              <button className="" type="button" onClick={() => this.setState({ togelSignUpMessage: !this.state.togelSignUpMessage })}><CloseIcon /></button>
            </div>
          </CollapseTransition>
        </footer>
        {
              this.state.loading
                ? <Loader />
                : null
          }
      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
  homeBgStyle: state.AdminPageReducers.homeBgStyle,
  products: state.ProductsPageReducers.products,
  cartItems: state.HomePageReducers.cartItems,
  publicPageUser: state.HomePageReducers.publicPageUser,
  publicPageProducts: state.HomePageReducers.publicPageProducts,
  publicPageShopDetails: state.HomePageReducers.publicPageShopDetails,
  shippingAddress: state.ProductsPageReducers.shippingAddress,
});

const mapDispatchToProps = (dispatch) => ({
  getUserDetails: (user) => store.dispatch({ type: GET_USER_DETAILS, payload: agent.Auth.getUserDetails(user) }),
  getPublicPageHeaderInfo: (user) => dispatch({ type: GET_PUBLIC_PAGE_HEADER_INFO, payload: agent.AdminPage.getHeaderInfo(user) }),
  getPublicPageProducts: (user) => dispatch({ type: GET_PUBLIC_PAGE_PRODUCTS, payload: agent.ProductsPage.getProducts(user) }),
  getPublicPageUserDetails: (user) => dispatch({ type: GET_PUBLIC_PAGE_USER, payload: agent.Auth.getUserDetails(user) }),
  setPublicPageUserDetails: (user) => dispatch({ type: GET_PUBLIC_PAGE_USER, payload: user }),

  getPublicPageShopDetails: (user) => dispatch({ type: GET_PUBLIC_PAGE_SHOP_DETAILS, payload: agent.ProductsPage.getShopDetails(user) }),

  clearCart: () => store.dispatch({ type: CLEAR_CART }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(HomePage);
